.header {
	border-bottom: 1px solid #ccc;
	padding: 10px 0;
	background-color: $color-white;

	&__nav {
		display: flex;
		align-items: center;

		&-link {
			margin: 0 20px;
			padding: 22px 0;

			&:hover {
				border-bottom: 3px solid rgba(59, 130, 246, 0.5);
			}
		}

		.is-active {
			border-bottom: 3px solid rgba(59, 130, 246, 0.5);
		}
	}

	&__body {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 auto;
		max-width: 80rem;
	}

	&__list {
		width: 100%;
		text-align: center;
	}

	&__link {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: left;
		margin: 0 auto;
		padding: 10px 30px;
		min-height: 85px;
		font-weight: 700;
		font-size: 16px;
		line-height: 25px;
		text-align: left;
		color: rgba($color-black, 0.7);
		background-color: $color-white;

		&:not(:last-child) {
			border-bottom: 1px solid $color-gray;
		}

		&:first-child {
			border-top: 1px solid $color-gray;
		}

		@include hover {
			color: $color-black;
		}

		@include active {
			color: $color-black;
		}
	}

	&__burger {
		z-index: 100;
		flex-direction: column;
		margin: (-6.5px / 2) 0;
		margin-left: auto;
		width: 39px;
		color: $color-black;

		span {
			display: block;
			margin: 6.5px 0;
			width: 39px;
			height: 2px;
			background-color: currentColor;
			transition: $duration-normal;
		}

		@include active {
			span {
				&:first-child {
					transform: translate(0, 8px) rotate(-45deg);
				}

				&:last-child {
					transform: translate(0, -6px) rotate(45deg);
				}
			}
		}
	}
}
