.button {
	border: 0;
	border-radius: 0.5rem;
	padding: 10px 24px;
	color: $color-white;
	background: #6c757d;
	transition: 0.2s;
	cursor: pointer;

	&:hover {
		background: #495057;
	}

	&:active {
		background: #343a40;
	}
}

.home {
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-weight: 500;

	.login {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		height: 70vh;

		form {
			padding: 30px;

			.errorlist {
				color: #c52c2c;
			}

			.helptext {
				display: none;
			}

			p {
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin: 15px 0;
			}

			label {
				margin-right: 30px;
			}

			input[type='text'], input[type='password'], input[type='email'] {
				border: 1px solid #ccc;
				padding: 6px 12px;
				border-radius: 0.5rem;
			}
		}

		.register {
			margin: 15px 0;
			font-weight: 100;
		}
	}

	.all-forms {
		border-radius: 0.5rem;
		padding: 16px;
		max-width: 80rem;
		background: #fff;
	}

	&__title {
		margin: 0 auto;
		border-bottom: 1px solid $color-light-gray;
		border-radius: 0.5rem 0.5rem 0 0;
		padding: 16px;
		max-width: 80rem;
		background: $color-white;
	}

	&__page {
		background: $color-white;

		&-name {
			margin: 0 auto;
			padding: 3px 55px;
			max-width: 80rem;
		}
	}

	&__search-form {
		margin: 72px auto 0;
		border-radius: 0.5rem;
		max-width: 80rem;
		background: $color-white;

		&-body {
			display: flex;
			justify-content: center;
			padding: 12px;
		}

		&__input,
		&__button {
			border: 1px solid $color-light-gray;
			padding: 6px 12px;
		}

		&__input {
			border-radius: 0.5rem 0 0 0.5rem;
			width: 85%;
			font-weight: 500;
			font-size: 23px;
		}

		&__button {
			border-radius: 0 0.5rem 0.5rem 0;
		}
	}

	&__settings {
		margin: 24px auto 0;
		border-radius: 0.5rem;
		padding: 16px;
		max-width: 80rem;
		background: $color-white;

		&-text {
			padding-right: 16px;
		}
	}

	&__generate-offer {
		position: fixed;
		right: 0;
		bottom: 0;
		z-index: 10;
		margin: 0 2em 2em 0;
		box-shadow: 0 0 10px $color-black;
	}

	&__setting {
		display: none;
		margin-top: 15px;
		border-top: 1px #ccc solid;
	}

	&__platform-scrap {
		display: flex;
		justify-content: space-around;
		margin: 15px 0 0;

		&-platform {
			cursor: pointer;

			input,
			label {
				cursor: pointer;
			}
		}
	}

	&__forms-title {
		margin: 24px auto 0;
	}

	&__forms-body {
		margin: 0 auto;
		width: 80rem;
		background: #fff;
	}

	&__generate_cp {
		border: 1px solid $color-light-gray;
		border-radius: 0.5rem;
		padding: 6px 12px;
		width: 100%;
		height: 18em;
		font-size: 16px;
		resize: none;
	}

	&__forms-table {
		margin: 0 auto;
		border-radius: 0 0 0.5rem 0.5rem;
		padding: 10px 10px 30px;
		max-width: 80rem;
		background: $color-white;

		table {
			border-collapse: collapse;
			width: 100%;

			td {
				padding: 15px;
				text-align: center;
			}
		}

		thead tr {
			background: #a1a9b1;
		}

		tbody .platforms {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			margin: 0 auto;
			width: 15%;

			p {
				padding: 2px 0;
			}
		}

		tbody tr:nth-child(odd) {
			background-color: #e9ecef;
		}

		tbody tr:nth-child(even) {
			background-color: #dee2e6;
		}

		tbody .for-view {
			display: flex;
			justify-content: center;
		}
	}

	.positions-forms {
		padding: 16px;
	}

	.position-form {
		display: flex;
		margin: 1em;
		border: 2px $color-light-gray solid;
		border-radius: 15px;
		background-color: #fff;

		.position__img,
		.position__body {
			margin: 0.5em;
		}

		.position__img {
			overflow: hidden;
			border: 1px $color-light-gray solid;
			border-radius: 15px;
			height: 200px;
			min-width: 200px;
			color: $color-light-gray;

			img {
				max-width: 200px;
				max-height: 200px;
				object-fit: cover;
			}
		}

		.position__body-cp {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
		}

		.position__body {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			width: 45em;
			font-size: 18px;

			.position__name {
				font-size: 25px;
				color: #30b9ff;
				transition: all 0.2s ease;

				a:hover {
					text-decoration: underline;
					transition: all 0.2s ease;
				}
			}
		}

		.position__price {
			font-size: 18px;
		}

		.position__cp {
			display: flex;
			flex-direction: column;
			justify-content: space-evenly;
			border-left: 1px $color-light-gray solid;
			padding-left: 10px;
			width: 10em;
			min-width: 10em;

			.cp__bu,
			.cp__new {
				display: flex;
			}

			.cp-input {
				margin-right: 5px;
				transform: scale(1.5);
				cursor: pointer;
			}

			.cp__bu label,
			.cp__new label {
				margin: 5px;
				font-size: 18px;
				cursor: pointer;
			}
		}
	}
}
